@import './common.less';
.pyg_view{
    display: flex;
    overflow: hidden;
    /* 
    默认情况下  
        子项的宽度 =   内容撑开 
        子项的高度 =    父项的高度  
     */
    .left_box{
      width: 80px;
      overflow: hidden;
      // height: 100%;
  
      .left_menu{
        list-style: none;
        margin: 0;
        padding: 0;
        li{
          height: 40px;
          font-size: 13px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .active{
          border-left: 5px solid #0094ff;
          background-color: #fff;
        }
      }
    }
    .right_box{
      flex: 1;
  
      overflow: hidden;
      // height: 100%;
      background-color: #fff;
  
      .rigth_item{
        .item_title{
          height: 40px;
     
          display: flex;
          padding-left: 15px;
          align-items: center;
          font-size: 14px;
          color: #0094ff;
        }
        .item_list{
          list-style: none;
          margin: 0;
          padding: 0;
          display: flex;
          flex-wrap: wrap;
          li{
            width: 33.33%;
            a{
              text-align: center;
              font-size: 13px;
              color: #000;
              img{
                width: 80%;
              }
              .item_name{}
            }
          }
        }
      }
    }
  }