html,
body,
.pyg_view {
  height: 100%;
}
body {
  padding-top: 45px;
  padding-bottom: 45px;
}
.pyg_view {
  overflow: scroll;
}
.pyg_header {
  padding: 5px;
  width: 100%;
  height: 45px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
}
.pyg_footer {
  width: 100%;
  height: 45px;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;
  border-top: 1px solid #ccc;
  display: flex;
}
.pyg_footer a {
  color: #000;
  font-size: 13px;
  text-align: center;
  padding-top: 5px;
  flex: 1;
}
.pyg_footer a i {
  font-size: 18px;
}
.loadding::before {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.5;
  content: "";
  z-index: 100;
}
.loadding::after {
  font: normal normal normal 14px/1 FontAwesome;
  position: fixed;
  top: 40%;
  left: 50%;
  margin-left: -50px;
  font-size: 100px;
  color: #0094ff;
  content: "\f110";
  z-index: 101;
  animation: fa-spin 1s infinite linear;
}
.pyg_view {
  display: flex;
  overflow: hidden;
  /* 
    默认情况下  
        子项的宽度 =   内容撑开 
        子项的高度 =    父项的高度  
     */
}
.pyg_view .left_box {
  width: 80px;
  overflow: hidden;
}
.pyg_view .left_box .left_menu {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pyg_view .left_box .left_menu li {
  height: 40px;
  font-size: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pyg_view .left_box .left_menu .active {
  border-left: 5px solid #0094ff;
  background-color: #fff;
}
.pyg_view .right_box {
  flex: 1;
  overflow: hidden;
  background-color: #fff;
}
.pyg_view .right_box .rigth_item .item_title {
  height: 40px;
  display: flex;
  padding-left: 15px;
  align-items: center;
  font-size: 14px;
  color: #0094ff;
}
.pyg_view .right_box .rigth_item .item_list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.pyg_view .right_box .rigth_item .item_list li {
  width: 33.33%;
}
.pyg_view .right_box .rigth_item .item_list li a {
  text-align: center;
  font-size: 13px;
  color: #000;
}
.pyg_view .right_box .rigth_item .item_list li a img {
  width: 80%;
}
